// ** React Imports
import { Fragment, useState, useEffect, forwardRef } from 'react'
import { Link, useParams } from 'react-router-dom'
// ** Table Columns
import { columns } from './columns'

// ** Store & Actions
import { getData, exportTask, deleteTaskBulk } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import Select from 'react-select'
import ReactPaginate from 'react-paginate'
import DataTable from 'react-data-table-component'
import { ChevronDown, Download, MoreVertical, Trash2, Upload } from 'react-feather'
import { Can } from '@src/utility/context/Can.js'
// ** Utils
import { selectThemeColors } from '@utils'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  ModalBody,
  Modal,
  ModalHeader,
  Badge
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { activeOrganization, orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'
//import toast from 'react-hot-toast'
const userId = orgUserId()
const activeOrg = activeOrganization()
// ** Table Header
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
const CustomHeader = ({ handlePerPage, rowsPerPage, handleFilter, searchTerm, exportList, selectedIds }) => {
  // ** Converts table to CSV 

  const dispatch = useDispatch()

  const deletefn = () => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteTaskBulk(selectedIds))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Task has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }


  return (
    <div className='invoice-list-table-header w-100 me-1 ms-50 mt-2 mb-75'>
      <Row>
        <Col xl='6' md='6' sm='6' className='d-flex align-items-center p-0'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>

            {selectedIds.length > 0 &&
              <Col onClick={() => { deletefn() }}>
                <Trash2 size={18} className='text-danger cursor-pointer ms-1' />
              </Col>
            }
          </div>
        </Col>
        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <Can I={'write'} a={'task'}>
              <Button className='add-new-user' color='primary' tag={Link} to='/task/add'>
                Add Task
              </Button>
            </Can>
            <UncontrolledDropdown className='ms-1'>
              <DropdownToggle color='secondary' caret outline>
                <MoreVertical className='font-small-4 me-50' />
              </DropdownToggle>
              <DropdownMenu>

                <Can I={'import'} a={'task'}>
                  <DropdownItem tag={Link} to={`/import/tasks`} className='w-100'>
                    <Upload className='font-small-4 me-50' />
                    <span className='align-middle'>Import</span>
                  </DropdownItem>
                </Can>
                <DropdownItem onClick={() => { exportList() }} className='w-100'>
                  <Download className='font-small-4 me-50' />
                  <span className='align-middle'>Export</span>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>

          </div>
        </Col>
      </Row>
    </div>
  )
}

const TaskList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector(state => state.task)

  // ** States
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const statusObj = [
    '',
    'form-check-primary',
    'form-check-warning',
    'form-check-success',
    'form-check-secondary',
    'form-check-secondary',
    'form-check-ocean',
    'form-check-info',
    'form-check-danger'
  ]

  //const [formModal, setFormModal] = useState(false)
  const [sort, setSort] = useState('desc')
  // const [newuserId, setNewUserId] = useState(null)
  // const [existuserId, setExistUserId] = useState(null)
  const { id } = useParams()
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [sortColumn, setSortColumn] = useState('createdon')
  const [rowsPerPage, setRowsPerPage] = useState(50)
  const [currentClient, setCurrentClient] = useState({ id: '', name: 'Select Client' })
  const [currentService, setCurrentService] = useState({ id: '', name: 'Select Service' })
  const [currentStatus, setCurrentStatus] = useState([])
  const [currentAssignee, setCurrentAssignee] = useState({ id: '', name: 'Select Assignee' })
  const [currentReviewer, setCurrentReviewer] = useState({ id: '', name: 'Select Reviewer' })
  const [currentDepartment, setCurrentDepartment] = useState({ id: '', name: 'Select Department' })
  const [currentSubStatus, setCurrentSubStatus] = useState({ id: '', statusname: 'Select Sub Status' })
  //const [noofTasks, setNoOfTasks] = useState(0)
  const today_date = moment().startOf('day').utc().format('x')
  // ** Bootstrap Checkbox Component
  const BootstrapCheckbox = forwardRef((props, ref) => (
    <div className='form-check'>
      <Input type='checkbox' ref={ref} {...props} />
    </div>
  ))
  // ** User filter options
  const statusOptions = [
    { value: 8, label: 'To Do' },
    { value: 1, label: 'Pending' },
    { value: 2, label: 'In Progress' },
    { value: 6, label: 'Sent to Review' },
    { value: 7, label: 'Request Change' },
    { value: 9, label: 'OverDue' },
    { value: 3, label: 'Completed' },
    { value: 4, label: 'On Hold' },
    { value: 5, label: 'Cancelled' }
  ]

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    let arr = []
    if (store.params?.status) {
      arr = store.params.status
      setCurrentStatus([...store.params.status])
    } else {
      setCurrentStatus([1, 2, 6, 7])
      arr = [1, 2, 6, 7]
    }
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        subStatus: currentSubStatus.id,
        assignee: currentAssignee.id,
        reviewer: currentReviewer.id,
        client: id || currentClient.id,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date,
        status: arr,
        service: store.params.service ? store.params.service : currentService.id
      })
    )
    setBlock(false)
  }, [dispatch])

  const [clientOptions, setClientOptions] = useState([])
  const [serviceOptions, setServiceOptions] = useState([])
  const [userOptions, setuserOptions] = useState([])
  const [departmentOptions, setDepartmentOptions] = useState([])
  const [subStatusOptions, setSubStatusOptions] = useState([])

  const getClients = () => {
    axios.post('/clients/dropdown', { status: 1 }).then(response => {
      const arr = response.data.clients
      arr.unshift({ id: '', name: 'Select All' })
      setClientOptions(arr)
      if (id) {
        const client = arr.find((obj) => obj.id === id)
        setCurrentClient(client)
      }
    })
  }

  const getServices = () => {
    axios.post('/services/dropdown', { status: 1 }).then(response => {
      const arr = response.data.services
      arr.unshift({ id: '', name: 'Select All' })
      setServiceOptions(arr)
    })
  }

  const getDepartment = () => {
    axios.post('/departments/dropdown', { status: 1 }).then(response => {
      const arr = response.data.departments
      arr.unshift({ id: '', name: 'Select All' })
      setDepartmentOptions(arr)
    })
  }

  const getSubStatus = () => {
    axios.post('/taskstatus/dropdown', { status: 1 }).then(response => {
      const arr = response.data.taskstatus
      arr.unshift({ id: '', statusname: 'Select All' })
      setSubStatusOptions(arr)
    })
  }

  const getOrganizationUsers = async () => {
    axios.post('/organizationusers/dropdown', { deleted: true }).then(response => {
      const arr = response.data.organizationusers
      arr.unshift({ id: '', name: 'Select All' })
      setuserOptions(arr)
    }).catch((err) => {
      console.log(err)
    })
  }

  const formatOptionLabel = ({ name, status }) => (
    <div className={`d-flex justify-content-between`} >
      <div>{name}</div>
      <div className={`ms-50 font-small-2`}>
        {status ? (parseInt(status) === -1 ? <Badge color='light-danger'>Deleted</Badge> : <Badge color='light-success'>Active</Badge>) : ''}
      </div>
    </div>
  )

  useEffect(() => {
    getOrganizationUsers()
    getServices()
    getDepartment()
    getClients()
    getSubStatus()
  }, [])


  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: page.selected + 1,
        department: currentDepartment.id,
        client: currentClient.id,
        status: currentStatus,
        service: currentService.id,
        subStatus: currentSubStatus.id,
        assignee: currentAssignee.id,
        reviewer: currentReviewer.id,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  // ** Function in get data on rows per page
  const handlePerPage = async e => {
    setBlock(true)
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: value,
        department: currentDepartment.id,
        page: currentPage,
        subStatus: currentSubStatus.id,
        client: currentClient.id,
        service: currentService.id,
        assignee: currentAssignee.id,
        reviewer: currentReviewer.id,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date,
        status: currentStatus
      })
    )
    setRowsPerPage(value)
    setBlock(false)
  }

  const exportList = async () => {
    setBlock(true)
    await dispatch(
      exportTask({
        sort,
        sortColumn,
        q: searchTerm,
        page: currentPage,
        subStatus: currentSubStatus.id,
        department: currentDepartment.id,
        perPage: rowsPerPage,
        assignee: currentAssignee.id,
        reviewer: currentReviewer.id,
        client: currentClient.id,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date,
        status: store.params.status || currentStatus,
        service: currentService.id
      })
    )
    setBlock(false)
  }

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        client: currentClient.id,
        department: currentDepartment.id,
        status: currentStatus,
        service: currentService.id,
        subStatus: currentSubStatus.id,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date
      })
    )
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      client: id || currentClient.id,
      service: currentService.id,
      department: currentDepartment.id,
      assignee: currentAssignee.id,
      subStatus: currentSubStatus.id,
      reviewer: currentReviewer.id,
      status: currentStatus,
      recurringFlag: false,
      orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
      date: today_date,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    }
  }

  const handleSort = async (column, sortDirection) => {
    setSort(sortDirection)
    setBlock(true)
    setSortColumn(column.sortField)
    await dispatch(
      getData({
        sort,
        sortColumn,
        department: currentDepartment.id,
        q: searchTerm,
        page: currentPage,
        perPage: rowsPerPage,
        client: currentClient.id,
        status: currentStatus,
        assignee: currentAssignee.id,
        subStatus: currentSubStatus.id,
        reviewer: currentReviewer.id,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date,
        service: currentService.id
      })
    )
    setBlock(false)
  }

  const handleStatus = async (val) => {
    let temp = []
    if (currentStatus.indexOf(val) === -1) {
      setCurrentStatus(current => [...current, val])
      temp = [...currentStatus, val]
    } else {
      setCurrentStatus((current) => current.filter((obj) => obj !== val))
      temp = currentStatus.filter((obj) => obj !== val)
    }

    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        client: currentClient.id,
        page: currentPage,
        perPage: rowsPerPage,
        department: currentDepartment.id,
        assignee: currentAssignee.id,
        subStatus: currentSubStatus.id,
        reviewer: currentReviewer.id,
        status: temp,
        recurringFlag: false,
        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
        date: today_date,
        service: currentService.id
      })
    )
    setBlock(false)
  }

  const RenderStatus = () => {

    const temp = statusOptions.filter((obj) => {
      if (activeOrg.taskcreateapproval && obj.value === 8) {
        return obj
      } else if (obj.value !== 8) {
        return obj
      }
    })

    return (
      <Col className='d-lg-flex justify-content-lg-evenly demo-inline-spacing' >
        {
          temp.map((obj, k) => {
            return (
              <div key={k} className={`custom-checkbox custom-control custom-control-inline ${statusObj[obj.value]}`} >
                <Input type='checkbox' value={obj.value} id={`opt_${k}`} onChange={() => handleStatus(parseInt(obj.value))} defaultChecked={currentStatus.indexOf(obj.value) > -1} />
                <Label className='form-label ms-25' for={`opt_${k}`}>{obj.label}</Label>
              </div>

            )
          })
        }
      </Col>
    )
  }

  const [selectedIds, setSelectedIds] = useState([])
  const handleChange = (state) => {
    const temp = []
    state.selectedRows.forEach((obj) => {
      temp.push(obj.id)
    })
    setSelectedIds(temp)
  }

  return (
    <Fragment>

      <Card>
        <CardBody>
          <Row>
            {!id &&
              <Col md={2} sm={12} lg={2} className='p-25'>
                <Select
                  label={'name'}
                  isClearable={false}
                  value={currentClient}
                  options={clientOptions}
                  className='react-select'
                  classNamePrefix='select'
                  theme={selectThemeColors}
                  getOptionLabel={(option) => option.name}
                  getOptionValue={(option) => option.id}
                  onChange={async data => {
                    setCurrentClient(data)
                    setBlock(true)
                    await dispatch(
                      getData({
                        sort,
                        sortColumn,
                        q: searchTerm,
                        client: data.id,
                        page: currentPage,
                        perPage: rowsPerPage,
                        status: currentStatus,
                        assignee: currentAssignee.id,
                        subStatus: currentSubStatus.id,
                        department: currentDepartment.id,
                        reviewer: currentReviewer.id,
                        recurringFlag: false,
                        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
                        date: today_date,
                        service: currentService.id
                      })
                    )
                    setBlock(false)
                  }}
                />
              </Col>
            }
            <Col md={2} sm={12} lg={2} className='p-25'>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={serviceOptions}
                value={currentService}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentService(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: searchTerm,
                      page: currentPage,
                      perPage: rowsPerPage,
                      client: currentClient.id,
                      assignee: currentAssignee.id,
                      subStatus: currentSubStatus.id,
                      reviewer: currentReviewer.id,
                      department: currentDepartment.id,
                      service: data.id,
                      recurringFlag: false,
                      orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
                      date: today_date,
                      status: currentStatus
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
            {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) && (
              <Col md={2} sm={12} lg={2} className='p-25'>
                <Select
                  isClearable={false}
                  value={currentAssignee}
                  options={userOptions}
                  className='react-select'
                  classNamePrefix='select'
                  formatOptionLabel={formatOptionLabel}
                  theme={selectThemeColors}
                  getOptionLabel={(option) => option.name}
                  getOptionValue={(option) => option.id}
                  onChange={async data => {
                    setCurrentAssignee(data)
                    setBlock(true)
                    await dispatch(
                      getData({
                        sort,
                        sortColumn,
                        q: searchTerm,
                        client: currentClient.id,
                        page: currentPage,
                        perPage: rowsPerPage,
                        assignee: data.id,
                        reviewer: currentReviewer.id,
                        status: currentStatus,
                        subStatus: currentSubStatus.id,
                        recurringFlag: false,
                        department: currentDepartment.id,
                        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
                        date: today_date,
                        service: currentService.id
                      })
                    )
                    setBlock(false)
                  }}
                />
              </Col>)
            }
            {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) && (
              <Col md={2} sm={12} lg={2} className='p-25'>
                <Select
                  isClearable={false}
                  value={currentReviewer}
                  options={userOptions}
                  formatOptionLabel={formatOptionLabel}
                  className='react-select'
                  classNamePrefix='select'
                  theme={selectThemeColors}
                  getOptionLabel={(option) => option.name}
                  getOptionValue={(option) => option.id}
                  onChange={async data => {
                    setCurrentReviewer(data)
                    setBlock(true)
                    await dispatch(
                      getData({
                        sort,
                        sortColumn,
                        q: searchTerm,
                        client: currentClient.id,
                        page: currentPage,
                        perPage: rowsPerPage,
                        assignee: currentAssignee.id,
                        reviewer: data.id,
                        status: currentStatus,
                        recurringFlag: false,
                        subStatus: currentSubStatus.id,
                        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
                        date: today_date,
                        department: currentDepartment.id,
                        service: currentService.id
                      })
                    )
                    setBlock(false)
                  }}
                />
              </Col>)
            }
            {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) && (
              <Col md={2} sm={12} lg={2} className='p-25'>
                <Select
                  isClearable={false}
                  value={currentDepartment}
                  options={departmentOptions}
                  className='react-select'
                  classNamePrefix='select'
                  theme={selectThemeColors}
                  getOptionLabel={(option) => option.name}
                  getOptionValue={(option) => option.id}
                  onChange={async data => {
                    setCurrentDepartment(data)
                    setBlock(true)
                    await dispatch(
                      getData({
                        sort,
                        sortColumn,
                        q: searchTerm,
                        client: currentClient.id,
                        page: currentPage,
                        perPage: rowsPerPage,
                        assignee: currentAssignee.id,
                        reviewer: currentReviewer.id,
                        status: currentStatus,
                        subStatus: currentSubStatus.id,
                        recurringFlag: false,
                        department: data.id,
                        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
                        date: today_date,
                        service: currentService.id
                      })
                    )
                    setBlock(false)
                  }}
                />
              </Col>)
            }
            {(parseInt(activeOrg.rolelevel) === 1 || parseInt(activeOrg.rolelevel) === 2) && (
              <Col md={2} sm={12} lg={2} className='p-25'>
                <Select
                  isClearable={false}
                  value={currentSubStatus}
                  options={subStatusOptions}
                  className='react-select'
                  classNamePrefix='select'
                  theme={selectThemeColors}
                  getOptionLabel={(option) => option.statusname}
                  getOptionValue={(option) => option.id}
                  onChange={async data => {
                    setCurrentSubStatus(data)
                    setBlock(true)
                    await dispatch(
                      getData({
                        sort,
                        sortColumn,
                        q: searchTerm,
                        client: currentClient.id,
                        page: currentPage,
                        perPage: rowsPerPage,
                        assignee: currentAssignee.id,
                        reviewer: currentReviewer.id,
                        status: currentStatus,
                        subStatus: data.id,
                        recurringFlag: false,
                        department: currentDepartment.id,
                        orguserid: parseInt(activeOrg?.rolelevel) === 1 ? '' : userId,
                        date: today_date,
                        service: currentService.id
                      })
                    )
                    setBlock(false)
                  }}
                />
              </Col>)
            }
          </Row>
          <Row className='mt-25'>
            <RenderStatus />
          </Row>
        </CardBody>
      </Card>

      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <div className='react-dataTable'>
            <DataTable
              noHeader
              subHeader
              sortServer
              pagination
              responsive
              selectableRows
              paginationServer
              columns={columns}
              highlightOnHover
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className='react-dataTable'
              paginationComponent={CustomPagination}
              data={dataToRender()}
              selectableRowsComponent={BootstrapCheckbox}
              onSelectedRowsChange={handleChange}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  exportList={exportList}
                  selectedIds={selectedIds}
                />
              }
            />
          </div>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default TaskList
